<template>
  <layout :title="title">
    <div class="category">
      <van-tabs v-model="activeName">
        <van-tab title="第一场" name="第一场">
          <img :src="poster.poster1" alt="第一场">
        </van-tab>
        <van-tab title="第二场" name="第二场">
          <img :src="poster.poster2" alt="第二场">
        </van-tab>
        <van-tab title="第三场" name="第三场">
          <img :src="poster.poster3" alt="第三场">
        </van-tab>
      </van-tabs>

      <div class="intro">
        <p>
          电影「船」SHIP是全球首部结合剧情、娱乐与数字艺术的NFT电影。
          ”船“由岸直彦先生Naohiko Kishi创作，
          影片地主要角色创新地全部由NFT艺术演绎。
        </p>
      </div>
      <div class="works">
        <div class="works-title">艺术品</div>
        <div class="works-list">
          <router-link class="item"
                       v-for="(work,index) in worksList" :key="work.name"
                       :to="`/detail/${index}`">
            <img :src="work.img" :alt="work.name">
            <span>{{ work.intro }}</span>
          </router-link>
        </div>
      </div>
      <div class="barrage">
        <div class="barrage-title">弹幕</div>
        <div class="barrage-content">
          <Danmaku :danmus="danmus"/>
        </div>
        <div class="barrage-input">
          <input type="text" placeholder="请输入弹幕内容"
                 v-model="inputDanmus"
                 @keyup.enter="sendDanmu">
          <span @click="sendDanmu">发送</span>
        </div>
      </div>
    </div>
  </layout>
</template>

<script>
import {
  work1, work2, work3,
} from '@/assets/images/category'
import poster1 from '@/assets/images/movies/第一幕头图.jpg'
import poster2 from '@/assets/images/movies/第二场.jpg'
import poster3 from '@/assets/images/movies/第三场.jpg'

import Danmaku from '@/components/Danmaku'
import avatar from '@/assets/images/movies/弹幕头像.jpg'
import avatar2 from '@/assets/images/movies/弹幕头像2.jpg'

export default {
  name: 'MovieView',
  components: {
    Danmaku
  },
  data() {
    return {
      activeName: '第一场',
      title: '第一幕',
      poster: {poster1, poster2, poster3,},
      worksList: [
        {
          name: '艺术品1',
          intro: '大坂拓也[i will always be you]',
          img: work1,
        },
        {
          name: '艺术品2',
          intro: '铃木初音[LUNA]',
          img: work2,
        },
        {
          name: '艺术品3',
          intro: '铃木初音[LUNA]',
          img: work3,
        }
      ],
      inputDanmus: '',
      danmus: [
        {avatar: avatar, author: '张三', content: '哈哈哈哈，我是弹幕君'},
        {avatar: avatar2, author: '李四', content: '哇，这个电影针不戳'},
        {avatar: avatar, author: '李四', content: 'hello, world!'},
      ],
    }
  },
  mounted() {
    const numberObj = {
      1: '一',
      2: '二',
      3: '三',
    }
    const strObj = this.objectFlip(numberObj)
    setInterval(() => {
      // 截取当前场次
      const i = this.activeName.slice(1, 2)
      // 获取当前下标的下一个
      let index = parseInt(strObj[i]) + 1
      this.activeName = `第${numberObj[index]}场`
    }, 3000)
  },
  methods: {
    sendDanmu() {
      if (!this.inputDanmus) return
      this.danmus.push({
        avatar: avatar2,
        author: '王五',
        content: this.inputDanmus,
      })
      this.inputDanmus = ''
    },
    objectFlip(obj) {
      return Object.keys(obj).reduce((ret, key) => {
        ret[obj[key]] = key
        return ret
      }, {})
    }
  },
}
</script>

